@import "~antd/dist/antd.css";

.menu {
  border-bottom: none;
  overflow: auto;
  box-shadow: 0 0 10px #000;
  transition-timing-function: ease-in;
  transition: all 0.5s;
  overflow: hidden;
  padding: 0 20px;
  background: rgba(0, 0, 0, 0.5) !important;
  z-index: 999;
}
.menu__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1400px;
  margin: auto;
}

.menu_center {
  position: absolute;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
  width: fit-content;
}

.ant-menu-item-group-list .anticon {
  font-size: 20px;
}
.ant-menu {
  background: transparent;
}
.ant-menu {
  transform: translateY(-1px);
}

.menu_left .ant-menu-item a,
.menu_center .ant-menu-item a {
  color: #fff;
  font-size: clamp(0.8rem, 1.5vw, 1rem);
  font-weight: 500;
}

.menu.active {
  background: #6a3460 !important;
}

.ant-badge-count {
  transform: translate(-25px, -8px);
}

.menu__container .ant-menu-item {
  padding-top: 1rem !important;
  margin: 0 10px !important;
}

.menu__container .ant-menu-horizontal {
  border-bottom: none;
}

.menu__mobile-button {
  float: right;
  height: 32px;
  padding: 6px;
  margin-top: 8px;
  display: none !important; /* use of important to overwrite ant-btn */
}

.menu_drawer .ant-drawer-body {
  padding: 0 !important;
  /* background-color: darkslategrey; */
}

.ant-modal-header {
  background: #3d7d9f;
}
.ant-modal-body {
  padding: 0;
}
.ant-modal-title {
  color: #fff;
}

.ant-modal-footer {
  display: none;
}

/* align header of Drawer with header of page */
.menu_drawer .ant-drawer-header {
  padding: 14px 24px !important;
  background: #6a3460 !important;
}

.menu_drawer .ant-drawer-title {
  color: #fff !important;
}

.menu_drawer .ant-menu:not(:last-child) .ant-menu-item {
  font-weight: bold;
  border-bottom: 1px solid #ddd;
  width: 200px;
  margin: 0.5rem 1rem 0.2rem 1rem;
}
.ant-drawer-body {
  background: #f5f5f5;
}
.ant-drawer-body .ant-menu-submenu-title {
  height: fit-content !important;
  padding: 1rem 5rem;
}

.ant-drawer-body .ant-menu:last-child .ant-menu-item {
  height: fit-content;
  padding: 0.5rem 0;
}

@media (max-width: 768px) {
  .menu__mobile-button {
    display: inline-block !important;
  }

  .menu_right,
  .menu_center {
    display: none;
  }

  .menu__logo a {
    margin-left: -20px;
  }

  .menu__container .ant-menu-item,
  .menu__container .ant-menu-submenu-title {
    padding: 1px 20px;
  }

  .menu__logo a {
    padding: 10px 20px;
  }
}
