.footer {
  background: #2e343d;
  position: relative;
  bottom: 0;
  width: 100%;
  text-align: center;
  padding-bottom: 1rem;
  min-height: 270px;
}

.footer .ant-divider {
  margin: 0;
}

.footerDivider {
  max-width: 1000px;
  margin: auto;
  padding: 1rem;
}
.footerDivider h1 {
  color: #fff !important;
}
.footer b,
.footerIcons .anticon {
  color: #b7b7b7;
}

.footerIcons .anticon {
  margin: 1rem;
  font-size: clamp(1.5rem, 3vw, 4rem);
  transition: 0.3s ease-out;
}

.footerIcons .anticon:hover {
  transform: scale(1.1);
}
.footerIcons .blue .anticon {
  color: #3d7d9f;
}

.footerIcons {
  display: flex;
  justify-content: center;
  align-items: center;
}
